.section_result {
  padding: 95px 0 70px;
  background-color: #edf8f6;
  .top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    height: initial;
    padding-bottom: 300px;
  }
  .left {
    display: flex;
    justify-content: center;
    position: relative;
    width: 40%;
    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
      width: 100%;
      height: 90%;
      z-index: 1;
      background-color: #adcbc6;
    }
    img {
      margin-top: 100px;
      max-width: 75%;
      z-index: 1;
    }
    .kursiv {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom:-40px;
      z-index: 1;
      color: #013e37;
    }
  }
  .center {
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .section_title {
      margin-top: 30px;
      padding-bottom: 120px;
      line-height: 0.8;
    }
  }
  .right {
    width: 15%;
  }
  .bottom {
    padding-top: 100px;
    @include media("<xl") {
      padding-top: 50px;
    }
    .slider_content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      height: 545px;
      width: 267px;

      @include media("<xl") {
        width: 200px;
        height: 400px;
      }
      h5,
      p {
        color: #013e37;
      }
      p {
        text-align: left;
        margin-left: 10px;
        font-weight: 400;
        font-size: 16px;
        @include media("<xl") {
          font-size: 10px;
        }
      }
    }
  }
}
